.subform {
    display: flex;
    @include r($m-lg) {
        display: block; }
    &__input {
        flex-grow: 1;
        padding-right: 40px;
        @include r($m-lg) {
            padding-right: 0; } }
    &__button {
        @include r($m-lg) {
            margin-top: 22px;
            text-align: center; } } }

.input {
    input {
        @include tt-reg;
        font-size: 20px;
        color: $grey-dark;
        width: 100%;
        height: 48px;
        background: $white;
        border-radius: 8px;
        padding: 4px 12px 0;
        margin-bottom: 12px;
        @include placeholder {
            color: $grey-light; }
        &::-ms-clear {
            display: none; } } }
