.counter {
    $this: &;
    // display: flex
    // flex-direction: row-reverse
    // justify-content: space-between
    @include coverdiv;
    &__item {
        padding-left: 20px;
        position: absolute;
        z-index: 1;
        bottom: 632px;
        &:nth-child(1) {
            left: 76%;
            @include r($d-md) {
                left: 78%; }
            @include r($d-sm) {
                left: 81%; }
            @include r($t-lg) {
                left: 70%;
                bottom: 562px; }
            @include r($m-lg) {
                bottom: 272px;
                left: 4%; }
            #{$this}__line {
                height: 128px;
                @include r($d-sm) {
                    height: 134px; }
                @include r($t-lg) {
                    height: 74px; } } }

        &:nth-child(2) {
            left: 56%;
            @include r($d-md) {
                left: 55%; }
            @include r($d-sm) {
                left: 57%; }
            @include r($t-lg) {
                left: 49%;
                bottom: 670px; }
            @include r($m-lg) {
                bottom: 224px;
                left: auto;
                right: 4%; }
            #{$this}__line {
                height: 210px;
                @include r($d-md) {
                    height: 208px; }
                @include r($d-sm) {
                    height: 212px; }
                @include r($t-lg) {
                    height: 238px; }
                @include r($m-lg) {
                    height: 37px;
                    left: auto;
                    right: 4px; } } }

        &:nth-child(3) {
            left: 38%;
            @include r($d-md) {
                left: 34%; }
            @include r($d-sm) {
                left: 32%; }
            @include r($t-lg) {
                bottom: 562px;
                left: 24%; }
            @include r($m-lg) {
                bottom: -45px;
                left: auto;
                right: 4%; }
            #{$this}__line {
                height: 266px;
                @include r($d-md) {
                    height: 276px; }
                @include r($d-sm) {
                    height: 278px; }
                @include r($t-lg) {
                    height: 201px; }
                @include r($m-lg) {
                    height: 180px;
                    left: auto;
                    right: 4px;
                    top: auto;
                    bottom: 44px;
                    background: linear-gradient($green-positive 0%, $white 100%); } } }

        &:nth-child(4) {
            left: 19%;
            @include r($d-md) {
                left: 8%; }
            @include r($d-sm) {
                left: percentage(30/1260); }
            @include r($t-lg) {
                bottom: 670px; }
            @include r($m-lg) {
                bottom: 25px;
                left: 4%; }
            #{$this}__line {
                height: 373px;
                @include r($d-md) {
                    height: 315px; }
                @include r($d-sm) {
                    height: 310px; }
                @include r($t-lg) {
                    height: 348px; }
                @include r($m-lg) {
                    height: 89px;
                    top: auto;
                    bottom: 44px;
                    background: linear-gradient($green-positive 0%, $white 100%); } } } }


    &__figure {
        @include tt-demi;
        color: $light;
        line-height: 1.03;
        font-size: 40px;
        transition: color .3s $easing-common;
        cursor: pointer;
        @include r($m-lg) {
            font-size: 20px;
            color: $green-positive; }
        span {
            font-size: 66px;
            margin-right: 15px;
            @include r($m-lg) {
                font-size: 40px; } }
        &.is-active {
            color: $green-positive; } }
    &__text {
        margin-top: -4px;
        color: $charcoal;
        position: absolute;
        top: 100%;
        left: 20px; }
    &__line {
        position: absolute;
        top: 44px;
        left: 4px;
        background: linear-gradient($white 0%, $green-positive 100%);
        width: 1px;
        height: 100%;
        &:before, &:after {
            content: "";
            position: absolute;
            background-color: $green-positive;
            border-radius: 50%; }
        &:before {
            top: 0;
            width: 5px;
            height: 5px;
            left: -2px; }
        &:after {
            bottom: 0;
            width: 7px;
            height: 7px;
            left: -3px; } }
    &_quadruple {
        #{$this}__text {
            width: 222px;
            @include r(1150) {
                width: 160px; }
            @include r($m-lg) {
                width: 177px; } }
        #{$this}__item {
            &:nth-child(1) {
                left: 73%;
                @include r($d-md) {
                    left: 76%; }
                @include r($d-sm) {
                    left: 78%; }
                @include r($t-lg) {
                    left: 73%;
                    bottom: 600px; }
                @include r($m-lg) {
                    bottom: 396px;
                    left: 4%; }
                #{$this}__line {
                    height: 131px;
                    @include r($d-md) {
                        height: 129px; }
                    @include r($t-lg) {
                        height: 99px; }
                    @include r($m-lg) {
                        height: 198px; } } }
            &:nth-child(2) {
                @include r($m-lg) {
                    bottom: 280px; }
                #{$this}__line {
                    @include r($m-lg) {
                        height: 95px; } }
                #{$this}__text {
                    @include r($m-lg) {
                        left: -110px; } } }
            &:nth-child(3) {
                #{$this}__text {
                    @include r($m-lg) {
                        left: -65px; } } } } }
    &_triple {
        #{$this}__text {
            width: 100vw;
            max-width: 368px;
            left: 35px;
            @include r($d-sm) {
                max-width: 120px; }
            @include r($m-lg) {
                left: 27px;
                margin-top: 3px; } }
        #{$this}__figure {
            @include r($m-lg) {
                font-size: 40px;
                span {
                    font-size: 66px; } } }
        #{$this}__item {
            padding-left: 35px;
            bottom: 487px;
            @include r($d-md) {
                bottom: 352px; }
            @include r($t-lg) {
                bottom: 304px; }
            @include r($m-lg) {
                padding-left: 27px; }
            &:nth-child(1) {
                left: 70%;
                @include r($d-md) {
                    left: 66%; }
                @include r($d-sm) {
                    left: 75%; }
                @include r($t-lg) {
                    left: 64.5%; }
                @include r($m-lg) {
                    left: 40%;
                    bottom: -33px; }
                @include r($m-sm) {
                    left: 40%;
                    bottom: -90px; }
                #{$this}__line {
                    height: 333px;
                    @include r($d-md) {
                        height: 213px; }
                    @include r($d-sm) {
                        height: 158px; }
                    @include r($t-lg) {
                        height: 165px; }
                    @include r($m-lg) {
                        bottom: 0px;
                        top: auto;
                        height: 142px; }
                    @include r($m-sm) {
                        height: 144px; } } }
            &:nth-child(2) {
                left: 45%;
                @include r($d-md) {
                    left: 37%; }
                @include r($d-sm) {
                    left: 41%; }
                @include r($t-lg) {
                    left: 40%; }
                @include r($m-lg) {
                    bottom: 210px;
                    left: auto;
                    right: 30%; }
                @include r($m-sm) {
                    bottom: 176px;
                    right: 10%; }
                #{$this}__line {
                    height: 316px;
                    @include r($d-md) {
                        height: 161px; }
                    @include r($d-sm) {
                        height: 179px; }
                    @include r($t-lg) {
                        height: 129px; }
                    @include r($m-lg) {
                        height: 143px; }
                    @include r($m-sm) {
                        height: 136px; } }
                #{$this}__figure {
                    @include r($m-lg) {
                        padding-right: 27px;
                        width: 100vw;
                        max-width: 147px; } }
                #{$this}__text {
                    @include r($m-lg) {
                        left: auto;
                        right: 27px; } } }
            &:nth-child(3) {
                left: 18.5%;
                @include r($d-md) {
                    left: 8%; }
                @include r($d-sm) {
                    left: 12%; }
                @include r($t-lg) {
                    left: 15.5%; }
                @include r($m-lg) {
                    left: 30%;
                    bottom: 210px; }
                @include r($m-sm) {
                    bottom: 176px;
                    left: 12%; }
                #{$this}__line {
                    height: 417px;
                    @include r($d-md) {
                        height: 191px; }
                    @include r($d-sm) {
                        height: 223px; }
                    @include r($t-lg) {
                        height: 226px; }
                    @include r($m-lg) {
                        background: linear-gradient($white 0%, $green-positive 100%);
                        right: auto;
                        bottom: auto;
                        left: 4px;
                        top: 44px;
                        height: 133px; }
                    @include r($m-sm) {
                        height: 167px; } } } } } }
