@include font('TT Commons Regular', 'ttcommons-regular-webfont');
@include font('TT Commons Medium', 'ttcommons-medium-webfont');
@include font('TT Commons Demibold', 'ttcommons-demibold-webfont');
@include font('Open Sans Regular', 'open-sans');

@mixin tt-reg {
    font-family: 'TT Commons Regular', Arial, Helvetica, sans-serif; }
@mixin tt-med {
    font-family: 'TT Commons Medium', Arial, Helvetica, sans-serif; }
@mixin tt-demi {
    font-family: 'TT Commons Demibold', Arial, Helvetica, sans-serif; }
@mixin open-sans {
    font-family: 'Open Sans Regular', Arial, Helvetica, sans-serif; }
