.resources {
    &__in {
        padding: 100px 0 60px;
        @include r($t-lg) {
            padding: 34px 0 60px; }
        @include r($m-lg) {
            padding: 34px 0 20px; } }
    &__title {
        margin-bottom: 100px;
        @include r($m-lg) {
            margin-bottom: 36px; } }
    &__articles {
        margin-bottom: 48px; }
    &__linkboxes {
        display: flex;
        @include r($t-lg) {
            display: block; } }
    .linkbox {
        @include r($t-lg) {
            margin-bottom: 24px; } }
    .article-box {
        &_main {
            .article-box__text h4 {
                @include r($m-lg) {
                    font-size: 40px; } } } } }
