.cases {
    position: relative;
    z-index: 2;
    &__in {
        display: flex;
        align-items: center;
        padding: 26px 0 350px;
        @include r($t-lg) {
            flex-direction: column;
            padding: 26px 0 300px; }
        @include r($m-lg) {
            padding: 26px 0 142px; } }
    &__title {
        margin-bottom: 24px;
        &_mob {
            display: none;
            @include r($t-lg) {
                display: block;
                width: 100%;
                margin-bottom: 48px; } }
        &:not(.cases__title_mob) {
            @include r($t-lg) {
                display: none; } } }
    &__text {
        max-width: 469px;
        margin-bottom: 64px;
        @include r($t-lg) {
            max-width: none; }
        @include r($m-lg) {
            margin-bottom: 30px; } }
    &__info {
        @include r($t-lg) {
            order: 1; } }
    &__img {
        @include r($t-lg) {
            order: 0;
            margin-bottom: 48px; }
        @include r($m-lg) {
            margin-bottom: 40px; }
        svg {
            width: 100%;
            @include r($m-lg) {
                height: 187px; } } }
    .mountains {
        height: 358px;
        bottom: -6px;
        top: auto; } }
